<template>
  <div class="ju-role">
    <b-container style="position: relative;">
      <img class="img-cloud" :src="require('@/assets/awan2.svg')" alt="">
      <b-row>
        <b-col>
          <div class="container-title">
            <div class="title-text title-main">
              <span class="span-bolder">ROLE NEEDED</span>
            </div>
          </div>
        </b-col>
      </b-row>
      <b-row class="py-3 join-job-row">

        <b-col md="4" class="p-3" v-for="voc in this.voclist" v-bind:value="voc.name" :key="voc.popularname">
          <b-button class="join-role-button" :to="{name: 'Register', params: {params:{job:voc.id}}}" >
            <div class="join-role-button-text">
              <div class="join-role-icon">{{voc.type}}</div>
              {{voc.popularname}}
            </div>
            </b-button>
        </b-col>
      
      </b-row>

    </b-container>
  </div>
</template>

<script>
export default {
  name: "JoinRole",
  data() {
        return {
            voclist: [],
        }
    },
    mounted() {
        console.log("mounted");
        this.axios.get(process.env.VUE_APP_API_URL + "/api/rolee_display")
        .then(response => {
            // this.voclist = response.data;
             let _vocl = [];
            response.data.forEach(v => {
              //loop check if same name
              let _broke = false;
              for (let i = 0; i < _vocl.length; i++) {
                if (_vocl[i].popularname === v.popularname){ ////append id to same id
                  _vocl[i].id.push(v.id);
                  _broke = true;
                  break;
                }
              }
              //esle
              if(!_broke) {
                ////append new objects with id as array 
                _vocl.push({id:[v.id], type:v.type, description:v.description, popularname:v.popularname})
              }
              this.voclist = _vocl;
            });
        })
        .catch(err => console.log(err));
    }
};
</script>

